import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"

import ResumeData from "../assets/json/resume-data.json"

const programming = ResumeData.skills.programming
const workflow = ResumeData.skills.workflow

const SkillsPage = () => (
  <Layout>
    <SEO title="Skills" />
    <section
      className="resume-section p-3 p-lg-5 d-flex align-items-center animated fadeIn"
      id="skills"
    >
      <div className="w-100">
        <h2 className="mb-5">Skills</h2>
        <div className="subheading mb-3">Programming Languages &amp; Tools</div>
        <ul className="list-inline dev-icons">
          {programming.map((_programming, index) => {
            return (
              <li
                className="list-inline-item"
                title={_programming.title}
                key={index}
              >
                <i className={_programming.icon_class}></i>
              </li>
            )
          })}
        </ul>
        <div className="subheading mb-3">Workflow</div>
        <ul className="fa-ul mb-0">
          {workflow.map((_workflow, index) => {
            return (
              <li key={index}>
                <i className="fa-li fa fa-check"></i>
                {_workflow}
              </li>
            )
          })}
        </ul>
      </div>
    </section>
  </Layout>
)

export default SkillsPage
